<template>
  <div
    class="app-mobile"
    :style="{
      '--app-mobile-overflow':
        browser?.name === 'ios' ?
         'scroll' :
         'auto'
    }"
  >
    <MobileHeader />
    <slot />
    <div class="app-mobile-footer" />
  </div>
</template>

<script>
import { detect } from '@/shared/utils/detectBrowser'
import MobileHeader from '@/client/components/Header/MobileHeader'

export default {
  name: 'MobileLayout',
  components: { MobileHeader },
  setup () {
    const browser = detect(navigator.userAgent)

    return {
      browser
    }
  }
}
</script>

<style lang="scss">
.app-mobile {
  height: 100vh;
  overflow-y: var(--app-mobile-overflow);
  overflow-x: hidden;
  -webkit-overflow-scrolling: touch;
}
.app-mobile-footer {
  height: 100px;
}
</style>
